<template>
  <v-card color="#241c31">
    <v-card-text>
      <v-row>
        <v-col cols="12" sm="8">
          <br/>Joining to <b>Mega3</b> is super easy!
          <ul>
            <li>Read the documents before doing anything</li>
            <li>Make sure your wallet is connected to Binance Smart Chain (BSC) network</li>
            <li>Make sure you are sending the correct value to official contract address</li>
            <li>Send <b>{{symbol}} {{ticket}}</b> to the following address or scan the qrcode by your wallet directly</li>
          </ul>
          <br/>BSC contract address: <b>{{address}}</b>
        </v-col>
        <v-col cols="12" sm="4" class="d-flex align-center justify-end">
          <v-img :src="qrcode" :max-width="$vuetify.breakpoint.smAndDown ? '100%' : 200" style="border-radius: 8px" />
        </v-col>
      </v-row>
    </v-card-text>
    <VueQRcode
        :value="address" 
        :color="{dark: '#241c31', light: '#CCCCCC'}" 
        type="image/jpeg" 
        :margin="2"
        :width="500"
        @change="onDataUrlChange"
        :quality="1"
        v-show="false"
      />
  </v-card>
</template>

<script>
import { mapState } from 'vuex'
import VueQRcode from 'vue-qrcode'
export default {
  name: 'JoinCard',
  components: {VueQRcode},
  computed: {
    ...mapState(['address', 'ticket', 'symbol'])
  },
  data: () => ({
    qrcode: null
  }),
  methods: {
    onDataUrlChange(dataUrl){
      this.qrcode = dataUrl
    }
  }
}
</script>