<script>
import { mapState } from 'vuex';
const moment = require('moment')

export default {
  name: 'App',
  computed: {
    ...mapState(['account', 'network', 'contract', 'events', 'ethers', 'participants', 'circulating']),
    list(){
      return [...this.events].sort((a, b) => {
        a = parseInt(a.args.timestamp)
        b = parseInt(b.args.timestamp)
        return a < b ? 1 : a > b ? -1 : 0
      }).slice((this.page - 1) * 10, this.page * 10 + 10)
    }
  },
  methods: {
    shorten(string){
      return string.slice(0, 6) + '...' + string.slice(-6)
    },
    formatDate(timestamp){
      return moment(timestamp*1000).format('YYYY MMM Do h:mm:ss')
    },
    open(url){
      window.open(url, '_blank')
    }
  },
  mounted(){
    this.$store.dispatch('init')
  }
};
</script>