<template>
  <v-row dense>
    <v-col cols="12">
      <v-card color="#241c31">
        <v-card-text>
          <v-text-field solo dense rounded hide-details v-model="search" label="Address Search" append-icon="mdi-account-search" @click:append="find()" background-color="primary" />
        </v-card-text>
        <!-- <v-divider/> -->
        <v-row dense class="mt-2 px-2 py-1">
          <v-col cols="12" sm="4" order="1"></v-col>
          <v-col cols="12" sm="4" order="2">
            <NodeCard :data="parent" label="PARENT" @retarget="init" />
          </v-col>
          <v-col cols="12" sm="4" order="3"></v-col>
          <v-col cols="12" sm="4" order="4">
            <NodeCard :data="sibling1" label="SIBLING" @retarget="init" />
          </v-col>
          <v-col cols="12" sm="4" :order="$vuetify.breakpoint.smAndDown ? 5 : 4">
            <NodeCard :data="current" label="CURRENT" order="4" />
          </v-col>
          <v-col cols="12" sm="4" :order="$vuetify.breakpoint.smAndDown ? 4 : 5">
            <NodeCard :data="sibling2" label="SIBLING" @retarget="init" />
          </v-col>
          <v-col cols="12" sm="4" order="6">
            <NodeCard :data="child1" label="CHILD" @retarget="init" />
          </v-col>
          <v-col cols="12" sm="4" order="7">
            <NodeCard :data="child2" label="CHILD" @retarget="init" />
          </v-col>
          <v-col cols="12" sm="4" order="8">
            <NodeCard :data="child3" label="CHILD" @retarget="init" />
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import NodeCard from '@/components/Node.vue'
import { mapState } from 'vuex'
const template = {
  registered: false,
  cachback: false,
  children: 0,
  paid: 0,
  account: '0x0000000000000000000000000000000000000000',
  parent: 0,
  child1: '0x0000000000000000000000000000000000000000',
  child2: '0x0000000000000000000000000000000000000000',
  child3: '0x0000000000000000000000000000000000000000'
}
export default {
  name: 'ExplorerView',
  components: {NodeCard},
  data: () => ({
    search: '',
    parent: {...template},
    current: {...template},
    sibling1: {...template},
    sibling2: {...template},
    child1: {...template},
    child2: {...template},
    child3: {...template},
  }),
  computed: {
    ...mapState(['contract', 'symbol', 'color', 'zero', 'events', 'participants', 'circulating'])
  },
  methods: {
    async init(address){
      this.current = await this.contract.nodes(address)
      this.child1 = this.current.child1 != this.zero ? await this.contract.nodes(this.current.child1) : {...template}
      this.child2 = this.current.child2 != this.zero ? await this.contract.nodes(this.current.child2) : {...template}
      this.child3 = this.current.child3 != this.zero ? await this.contract.nodes(this.current.child3) : {...template}
      this.parent = this.current.parent != this.zero ? await this.contract.nodes(this.current.parent) : {...template}
      const siblings = [this.parent.child1, this.parent.child2, this.parent.child3].filter(account => account != this.current.account)
      this.sibling1 = siblings[0] ? await this.contract.nodes(siblings[0]) : {...template}
      this.sibling2 = siblings[1] ? await this.contract.nodes(siblings[1]) : {...template}
    },
    find(){
      if (/^0x[0-9a-fA-F]{40}$/.test(this.search)) this.init(this.search)
      else console.error('SEARCH', 'Invalid address')
    }
  },
  async created(){
    const cursor = await this.contract.cursor()
    const index = await this.contract.index(cursor)
    this.init(index)
    this.contract.on('Join', () => this.init(this.current.account))
  }
}
</script>