import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const ethers = require('ethers')
const provider = new ethers.providers.JsonRpcProvider('https://bsc-dataseed4.ninicoin.io/')
const abi = require('@/store/abi')
const address = '0x60B1936842ba3ff9aEBb8828a0b4Bd161F6AC679'

export default new Vuex.Store({
  state: {
    address,
    ethers,
    provider,
    contract: new ethers.Contract(address, abi, provider),
    ticket: 0,
    participants: 0,
    circulating: 0,
    symbol: 'BNB',
    color: 'yellow',
    zero: '0x0000000000000000000000000000000000000000',
    network: null,
    account: null,
    events: {
      join: [],
      sent: []
    },
  },
  getters: {
  },
  mutations: {
    info(state, data){
      state.participants = data.participants
      state.circulating = ethers.utils.formatEther(data.circulating)
    },
    join(state, data){
      state.events.join.push(data)
      if (state.events.join.length > 100) state.events.join.shift()
    },
    sent(state, data){
      state.events.sent.push(data)
      if (state.events.sent.length > 100) state.events.sent.shift()
    },
    participants(state, value){
      state.participants = value
    },
    circulating(state, value){
      state.circulating = ethers.utils.formatEther(value)
    },
    ticket(state, value){
      state.ticket = ethers.utils.formatEther(value)
    }
  },
  actions: {
    async init({state, commit}){
      commit('ticket', await state.contract.ticket())
      commit('info', {
        circulating: await state.contract.circulating(),
        participants: await state.contract.participants()
      })
      const start = await state.provider.getBlock()
      const events = await state.contract.queryFilter('*', start.number - 4999, 'latest')
      state.events.join = events.filter(i => i.event == 'Join')
      state.events.sent = events.filter(i => i.event == 'Sent')
      state.contract.on('Info', (participants, circulating) => commit('info', {participants, circulating}))
      state.contract.on('Sent', (user, value, timestamp, data) => commit('sent', data))
      state.contract.on('Join', (user, value, timestamp, data) => commit('join', data))
    }
  },
  modules: {
  }
})
