<template>
  <v-card dark :disabled="!data.registered" outlined>
    <div style="background-color: #241c31">
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class="overline" :class="currentColor">{{label}}</v-list-item-title>
          <v-list-item-subtitle class="caption">{{data.account == zero ? '--' :shorten(data.account)}}</v-list-item-subtitle>
        </v-list-item-content>
        <v-list-item-action class="text-center">
          <div class="text-h5 text--accent-4" :class="currentColor">{{$store.state.ethers.utils.formatEther(data.paid)}}</div>
        </v-list-item-action>
      </v-list-item>
      <v-card-text class="py-0">
        <div class="caption d-flex align-center">
          <span class="font-weight-bold text--accent-4" :class="currentColor">{{data.children}}</span>
          <span class="mx-1">/</span>
          <span class="font-weight-bold">{{children}}</span>
          <v-spacer/>
          <v-icon x-small>mdi-radiobox-{{data.child1 != zero ? 'marked' : 'blank'}}</v-icon>
          <v-icon x-small>mdi-radiobox-{{data.child2 != zero ? 'marked' : 'blank'}}</v-icon>
          <v-icon x-small>mdi-radiobox-{{data.child3 != zero ? 'marked' : 'blank'}}</v-icon>
        </div>
        <v-progress-linear rounded color="accent" :value="progress" />
      </v-card-text>
      <v-card-actions class="ma-0 pa-3">
        <v-btn x-small icon @click="retarget()"><v-icon>mdi-target-account</v-icon></v-btn>
      </v-card-actions>
    </div>
  </v-card>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'NodeCard',
  props: ['data', 'label'],
  data: () => ({
    
  }),
  computed: {
    ...mapState(['symbol', 'zero']),
    currentColor(){
      return this.data.registered ? 'accent--text' : 'grey--text'
    },
    children(){
      let total = 0
      for (let i = 1; i <= 9; i++) total += Math.pow(3, i)
      return total
    },
    progress(){
      return this.data.children / this.children * 100
    }
  },
  methods: {
    shorten(string){
      return string.slice(0, 6) + '...' + string.slice(-6)
    },
    retarget(){
      this.$emit('retarget', this.data.account)
    }
  }
}
</script>